        <details className="details">
          <summary className="title">
            Функция принадлежности (Практика-4)
          </summary>
          <section className="content">
            <p>
              <strong>2.1 Методы построения функции принадлежности</strong>
            </p>
            <p>
              Практическое использование теории нечетких множеств предполагает
              наличие функций принадлежностей, которыми описываются
              лингвистические термы &laquo;низкий&raquo;, &laquo;средний&raquo;,
              &laquo;высокий&raquo; и т.п. Задача построения функций
              принадлежности ставится следующим образом. Даны два множества:
              множество термов L={"{l1,l2,…,lm}"} и универсальное множество U=
              {"{u1,u2,…,um}"}. Нечеткое множество l ̃ для задания
              лингвистического терма l<sub>j</sub> на универсальном множестве U
              представляется в виде:
            </p>
            <img src={require("../asset/7.png")} alt="1.1" width={400} />
            <p>
              Необходимо определить степени принадлежности элементов множества U
              к элементам из множества L, т.е. найти μ
              <sub>
                t<sub>j</sub>
              </sub>
              (u<sub>i</sub>) для всех j=(1, m) и i=(1, n).
            </p>
            <p>
              Ниже рассматриваются два метода построения функций принадлежности.{" "}
              <strong>
                <em>
                  Первый метод основан на статистической обработке мнений группы
                  экспертов.
                </em>
              </strong>{" "}
              <em>
                Второй метод базируется на парных сравнениях, выполняемых одним
                экспертом.
              </em>{" "}
              В конце приведены аналитические выражения, которые часто
              используются для аппроксимации функций принадлежностей,
              построенных по экспертной информации.
            </p>
            <p>
              При построении функций принадлежности{" "}
              <strong>
                <em>по первому методу</em>
              </strong>{" "}
              каждый эксперт заполняет анкету, в которой указывает свое мнение о
              наличии у элементов μ
              <sub>
                t<sub>j</sub>
              </sub>
              (u<sub>i</sub>)(i=(1, ̅n)) свойств нечеткого множества l ̃(j=(1, m)
              ). Анкета имеет следующий вид:
            </p>
            <img src={require("../asset/8.png")} alt="1.1" width={600} />
            <p>
              Введем обозначения : K - количество экспертов; b<sub>j,i</sub>
              <sup>k</sup> мнение k-го эксперта о наличии у элемента u
              <sub>i</sub> свойств нечеткого множества l<sub>j</sub>, где
              k=(1,K), i=(1,n), j=(1,m). Будем считать, что экспертные оценки
              бинарные, т.е. b<sub>j,i</sub>
              <sup>k</sup>∈{"{0;1}"}, , где 1 указывает на наличие у элемента u
              <sub>i</sub> свойств нечеткого множества l ̃<sub>j</sub>, а 0 на их
              отсутствие. По результатам анкетирования степени нечеткому
              множеству l ̃<sub>j</sub> рассчитываются так:
            </p>
            <img src={require("../asset/9.png")} alt="1.1" width={250} />
            <p>Таблица 1 - Результаты опроса экспертов</p>
            <img src={require("../asset/10.png")} alt="1.1" width={750} />
            <p>
              При построении функций принадлежности{" "}
              <strong>
                <em>по второму методу</em>
              </strong>{" "}
              для каждой пары элементов универсального множества эксперт
              оценивает преимущество одного элемента над другим по отношению к
              свойству нечеткого множества. Такие парные сравнения удобно
              представлять следующей матрицей:
            </p>
            <img src={require("../asset/11.png")} alt="1.1" width={300} />
            <p>
              где a<sub>ij</sub> уровень преимущества элемента u<sub>i</sub> над
              u<sub>j</sub>, определяемый по девятибалльной шкале Саати:
            </p>
            <p>
              - если преимущество элемента u<sub>i</sub> над элементов u
              <sub>j</sub> отсутствует;
            </p>
            <p>
              - если преимущество элемента u<sub>i</sub> над элементов u
              <sub>j</sub> слабое;
            </p>
            <p>
              - если преимущество элемента u<sub>i</sub> над элементов u
              <sub>j</sub> существенное;
            </p>
            <p>
              - если преимущество элемента u<sub>i</sub> над элементов u
              <sub>j</sub> явное;
            </p>
            <p>
              - если преимущество элемента u<sub>i</sub> над элементов u
              <sub>j</sub> абсолютное;
            </p>
            <p>
              Промежуточные сравнительные оценки: 2 &ndash; почти слабое
              преимущество,4 &ndash; почти существенное&nbsp; преимущество, 6
              &ndash; почти явное преимущество и 8 &ndash; почти абсолютное
              преимущество.
            </p>
            <p>
              Матрица парных сравнений является диагональной и обратно
              симметричной.
            </p>
            <p>
              Степени принадлежности принимают равными соответствующим
              координатам вектора W=(w<sub>1</sub>,w<sub>2</sub>,…,w
              <sub>n</sub>)<sup>T</sup> матрицы парных сравнений А:
            </p>
            <img src={require("../asset/12.png")} alt="1.1" width={200} />
            <p>Соответственно вектор находят из следующей системы сравнений:</p>
            <img src={require("../asset/13.png")} alt="1.1" width={250} />
            <p>
              где λ<sub>max</sub> максимальное собственное значение матрицы А.
            </p>
          </section>
        </details>
